import { useState, useRef, useEffect } from "react";
import { useForm, Controller } from "react-hook-form";
import { useParams } from "react-router-dom";
import ToggleablePanel from "../../components/panels/ToogleablePanel";
import { InputNumber } from "primereact/inputnumber";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { Dropdown } from "primereact/dropdown";
import { classNames } from "primereact/utils";
import Footer from "../../components/layout/footer/Footer";
import {
  getMaintainanceCycleDetail,
  createMaintainanceCycle,
  updateMaintainanceCycle,
} from "../../services/maintainance-cycle-service";
import { getCarTypes, getManufacturers } from "../../services/car-service";

const MaintainanceCycleDetailPage = () => {
  const [existedSpareParts, setExistedSpareParts] = useState([]);
  const [carTypes, setCarTypes] = useState();
  const [manufacturers, setManufacturers] = useState();
  
  const formRef = useRef();
  const params = useParams();
  const selectedMaintainanceCycleId = params?.id;

  const defaultValues = {
    CarTypeId: "",
    ManufaturerId: "",
    YearOfManufactureFrom: "",
    YearOfManufactureTo: "",
    Note: "",
    TemplateDetails: [],
  };
  const [formValue, setFormValue] = useState(defaultValues);

  //get carTypes
  useEffect(() => {
    getCarTypes().then((response) => {
      setCarTypes(response.data.Result);
    });
  }, []);

  //get Manufacturers
  useEffect(() => {
    getManufacturers().then((response) => {
      setManufacturers(response.data.Result);
    });
  }, []);

  const {
    control,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm({ defaultValues });

  useEffect(() => {
    if (selectedMaintainanceCycleId) {
      getMaintainanceCycleDetail(selectedMaintainanceCycleId).then(
        (response) => {
          const maintainanceCycle = response.data.Result;
          setExistedSpareParts(maintainanceCycle.TemplateDetails);
          reset(maintainanceCycle);
          setFormValue(maintainanceCycle);
        }
      );
    }
  }, [reset, selectedMaintainanceCycleId]);

  const functionButtons = [
    {
      label: "Lu",
      icon: "pi pi-check",
      className: "p-button-success",
      action: () => {
        formRef.current.requestSubmit();
      },
    },
  ];

 

  // const handleCarsChange = (cars) => {
  //   setFormValue((values) => ({ ...values, Cars: cars }));
  // };

  const onSubmit = (formData, e) => {
    e.nativeEvent.preventDefault();
    if (formValue.CustomerId) {
      updateMaintainanceCycle({ ...formValue, ...formData });
    } else {
      createMaintainanceCycle({ ...formValue, ...formData });
    }
    setFormValue((values) => ({ ...values, ...formData }));
  };

  const getFormErrorMessage = (name) => {
    return (
      errors[name] && <small className="p-error">{errors[name].message}</small>
    );
  };

  return (
    <div className="relative h-full pb-8">
      <ToggleablePanel header="Thng tin xe" className="pb-2" toggleable>
        <form
          ref={formRef}
          onSubmit={handleSubmit(onSubmit)}
          className="formgrid grid"
        >
          <div className="field col-12 md:col-6">
            <label htmlFor="CarTypeId">
              Dng xe <b className="p-error">*</b>
            </label>
            <Controller
              name="CarTypeId"
              control={control}
              rules={{ required: "Dng xe khng c  trng!" }}
              render={({ field, fieldState }) => (
                <Dropdown
                  id={field.name}
                  value={field.value}
                  onChange={(e) => field.onChange(e.value)}
                  optionLabel="TypeName"
                  optionValue="TypeId"
                  options={carTypes}
                  placeholder="Chn dng xe"
                  filter
                  filterBy="TypeName"
                  className={classNames("w-full", {
                    "p-invalid": fieldState.error,
                  })}
                />
              )}
            />
            {getFormErrorMessage("CarTypeId")}
          </div>

          <div className="field col-12 md:col-6">
            <label htmlFor="ManufaturerId">
              Hng xe <b className="p-error">*</b>
            </label>
            <Controller
              name="ManufaturerId"
              control={control}
              rules={{ required: "Hng xe khng c  trng!" }}
              render={({ field, fieldState }) => (
                <Dropdown
                  id={field.name}
                  value={field.value}
                  onChange={(e) => field.onChange(e.value)}
                  optionLabel="ManufacturerName"
                  optionValue="ManufacturerId"
                  options={manufacturers}
                  placeholder="Chn hng xe"
                  filter
                  filterBy="ManufacturerName"
                  className={classNames("w-full", {
                    "p-invalid": fieldState.error,
                  })}
                />
              )}
            />
            {getFormErrorMessage("ManufaturerId")}
          </div>

          <div className="field col-12 md:col-6">
            <label htmlFor="YearOfManufactureFrom">
              Nm sn xut t <b className="p-error">*</b>
            </label>
            <Controller
              name="YearOfManufactureFrom"
              control={control}
              rules={{ required: "Nm sn xut t khng c  trng!" }}
              render={({ field }) => (
                <InputNumber
                  useGrouping={false}
                  id={field.name}
                  {...field}
                  className="w-full"
                />
              )}
            />
            {getFormErrorMessage("YearOfManufactureFrom")}
          </div>

          <div className="field col-12 md:col-6">
            <label htmlFor="YearOfManufactureTo">
              Nm sn xut n <b className="p-error">*</b>
            </label>
            <Controller
              name="YearOfManufactureTo"
              control={control}
              rules={{ required: "Nm sn xut n khng c  trng!" }}
              render={({ field }) => (
                <InputNumber
                  useGrouping={false}
                  id={field.name}
                  {...field}
                  className="w-full"
                />
              )}
            />
            {getFormErrorMessage("YearOfManufactureTo")}
          </div>

          <div className="field col-12 md:col-6">
            <label htmlFor="Note">Ghi Ch</label>
            <Controller
              name="Note"
              control={control}
              render={({ field }) => (
                <InputTextarea
                  rows={5}
                  cols={30}
                  id={field.name}
                  {...field}
                  className="block w-full"
                />
              )}
            />
          </div>
        </form>
      </ToggleablePanel>
      <ToggleablePanel
        header="Ph tng"
        className="pb-2"
        toggleable
      ></ToggleablePanel>
      <Footer items={functionButtons} />
    </div>
  );
};

export default MaintainanceCycleDetailPage;
